@use 'vars';
@use 'mixins' as *;

// - - - - - - - - - - - - - - - - - -

// Blog

// - - - - - - - - - - - - - - - - - -

.blog {
	margin-top: 0;
	padding: 20px 0 0;

	@include mq(tabletp) {
		padding: 35px 0 0;
	}

	@include mq(tabletl) {
		padding: 35px 40px 0;
	}

	@include mq(laptop) {
		padding: 53px 60px 0;
	}
}

.blog-post {
	margin-top: 25px;

	@include mq(tabletp) {
		margin-top: 60px;
	}

	@include mq(tabletl) {
		margin-top: 80px;
	}

	@include mq(laptop) {
		margin-top: 70px;
	}

	&:first-child {
		margin-top: 0;
	}
}

.blog-post__image {
	margin-top: 15px;
	margin-bottom: 10px;
	margin-left: auto;
	margin-right: auto;
	max-width: 30vh;
	height: auto;
	display: block;
	text-align: center;

	@include mq(tabletl) {
		margin-top: 20px;
		margin-bottom: 15px;
	}

	@include mq(laptop) {
		margin-top: 25px;
		margin-bottom: 20px;
	}
}

.blog-post__content {
	margin-top: 0px;

	@include mq(tabletp) {
		margin-top: 0px;
	}

	@include mq(tabletl) {
		margin-top: 0px;
	}

	@include mq(laptop) {
		margin-top: 0px;
	}

	p .button {
		margin-top: 5px;

		@include mq(tabletl) {
			margin-top: 10px;
		}
	}
}

.blog-post__title {
    margin-top: 25px;
	
}

.blog-post__subtitle {
	margin-top: 2px;
	color: vars.$text-light-color;
}

.pagination {
	margin-top: 0;
	padding: 40px 20px 0;

	@include mq(tabletp) {
		padding: 55px 40px 0;
	}

	@include mq(tabletl) {
		padding: 75px 40px 0;
	}

	@include mq(laptop) {
		padding: 93px 60px 0;
	}
}

.pagination__prev,
.pagination__next {
	width: 50%;

	.button {
		display: block;
		text-align: center;
	}
}

.pagination__prev {
	float: left;
	padding-right: calc(vars.$grid-spacing / 2);
}

.pagination__next {
	float: right;
	padding-left: calc(vars.$grid-spacing / 2);
}
